import React from "react";
import {
  MailOutlined,
  EyeOutlined,
  RightCircleFilled,
} from "@ant-design/icons";
import {
  Layout,
  Button,
  Checkbox,
  Form,
  Input,
  Row,
  Col,
} from "antd";

const handleLogin=(values)=>{
  console.log("values::::",values)
  let payload=
  {
    email:values.email,
    password:values.password
  }

  fetch(
    'http://127.0.0.1:8000/login/api/login/',
    {
      method: 'post',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payload),
    }
  )
    .then(response => {
      // Check if the response status is OK (status code 200)
      if (response.ok) {
        // Parse the JSON data from the response
        return response.json();
      } else {
        // Handle non-successful responses (e.g., display an error message)
        throw new Error(`Request failed with status: ${response.status}`);
      }
    })
    .then(data => {
      // Access the JSON data and log it to the console
      console.log(data.data);
      console.log(data.data.token);
      console.log(data.data.email);
      console.log(data.data.name);
  
      // You can now access properties like data.token, data.name, etc.
      // For example:
      // console.log(data.data.token);
      // console.log(data.data.name);
    })
    .catch(error => {
      // Handle errors during the fetch request
      console.error('Fetch error:', error);
    });
  
}
 const Login = () => {

  return (
    <Layout
      style={{ background: "#274361", minHeight: "100vh", overflow: "hidden" }}
      className="App"
    >
      <div className="container-fluid m-2 ">
        <div className="m-4 text-center">
          <img src="static/image1.png" height={52} width={262} />
        </div>
        <div className="row mt-4">
          <div className="col-8 text-center">
            <img
              src="static/loginMain.png"
              height={713}
              width={1103}
              className="m-2"
            />
          </div>
          <div className="col-4 mt-5" style={{ marginLeft: "-17rem" }}>
            <div
              className="card "
              style={{
                height: "420px",
                width: "460px",
                background: "#F7F7F780",
                opacity: "0.8 #F7F7F780 ",
              }}
            >
              <div
                className="card-Header p-2 m-5"
                style={{ borderRadius: "30px" }}
              >
                <h4
                  style={{ color: "#274361", weight: "500px" }}
                  className="fw-bold text-center"
                >
                  Welcome to Meaningful Vision
                </h4>
                <p className="text-center fw-bold">
                  A foodservice competitor scanner
                </p>
                <div className="mb-2">Please enter your details here</div>
                <div className="ml-4">
                  <Form
                    name="basic"
                    labelCol={{
                      span: 8,
                    }}
                    wrapperCol={{
                      span: 24,
                    }}
                    style={{
                      maxWidth: "100%",
                    }}
                    initialValues={{
                      remember: true,
                    }}
                    autoComplete="off"
                    className="ml-2"
                    onFinish={handleLogin}
                  >
                    <Form.Item
                      name="email"
                      rules={[
                        {
                          // required: true,
                          message: "Please input your email!",
                        },
                      ]}
                    >
                      <Input
                        placeholder="Email"
                        size="large"
                        prefix={<MailOutlined />}
                      />
                    </Form.Item>

                    <Form.Item
                      name="password"
                      rules={[
                        {
                          // required: true,
                          message: "Please input your password!",
                        },
                      ]}
                    >
                      <Input.Password
                        placeholder="Password"
                        size="large"
                        style={{ background: "white" }}
                        prefix={<EyeOutlined />}
                      />
                    </Form.Item>

                    <Row>
                      <Col>
                        <Form.Item
                          name="remember"
                          // valuePropName="checked"
                        >
                          <Checkbox size="large">Remember me ?</Checkbox>
                        </Form.Item>
                      </Col>
                      <Col>
                        <Form.Item>
                          <a href="" className="p-1 text-dark">
                            Forget password
                          </a>
                        </Form.Item>
                      </Col>
                    </Row>
                    <Form.Item>
                      <Button
                        type="primary"
                        htmlType="submit"
                        block
                        style={{ background: "#0E2A48", borderRadius: "10px" }}
                        size="large"
                      >
                        Login
                        <RightCircleFilled className="p-1" />
                      </Button>
                    </Form.Item>
                  </Form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};
export default Login;
